.figure {
    width: 300px;
    height: 200px;
    background-position: center;
    background-size: 100% 100%; 
    margin: 20px auto;
}

.rectangle {
    background-image: url(assets/images/geometry/rectangle.png)
}

.rectangle-triangle {
